<template>
  <div class="da-scroller" :style="{ height: scrollerHeight}">
    <div id="scrollerWrap" ref="scrollerWrap" class="da-scroller__wrap" :style="{ marginRight: verticalShow?'-10px':'0'}" @scroll="handleScroll">
      <div id="scrollerView" ref="scrollerView" class="da-scroller__view">
        <slot />
      </div>
    </div>
    <!-- 纵向滚动条 -->
    <div class="da-scroller__bar is-vertical" v-show="verticalShow">
      <div class="da-scroller__track">
        <div
          id="verticalBar"
          ref="verticalBar"
          @mousedown.left="verticalMousedown"
          class="da-scroller__thumb"
          :style="{'height': vbHeight, 'transform':`translateY(${ vbTranslate })`, 'userSelect': 'none'}"
        ></div>
      </div>
    </div>
    <!-- 横向滚动条 -->
    <!-- <div class="da-scroller__bar is-horizontal">
      <div class="da-scroller__track">
        <div class="da-scroller__thumb"></div>
      </div>
    </div> -->
  </div>
</template>
<script>
export default {
  name: 'Scroller',
  props: {
    scrollerHeight: {
      type: String,
      default: '100%'
    }
  },
  computed: {
    wrap() {
      return document.getElementById('scrollerWrap').offsetHeight
    },
    view() {
      return document.getElementById('scrollerView').offsetHeight
    },
    visible() {
      return this.view > this.wrap
    }
  },
  data() {
    return {
      verticalShow: false,
      vbHeight: '0%',
      vbTranslate: '0%',
    }
  },
  mounted() {
    let _this = this,
        verticalBar = _this.$refs.verticalBar,
        percent = _this.wrap / _this.view,
        barHeight = _this.wrap * percent
    _this.verticalShow = _this.visible
    _this.vbHeight = barHeight + 'px'
  },
  methods: {
    handleScroll(e) {
      let _this = this,
          view = _this.$refs.scrollerView,
          verticalBar = _this.$refs.verticalBar,
          percent = e.target.scrollTop / _this.wrap 
      _this.vbTranslate = percent * 100 + '%'
    },
    // 鼠标左键按下滚动条
    verticalMousedown(e) {
      let _this = this
      // console.log('verticalMousedown', e);
      document.addEventListener("mousemove", _this.handleMousemove) // 监听鼠标左键移动
      document.addEventListener("mouseup", _this.handleMouseup) // 监听鼠标左键抬起
    },
    // 鼠标左键抬起
    handleMouseup(e) {
      let _this = this
      // console.log('clear', e);
      document.removeEventListener("mousemove", _this.handleMousemove)
      document.removeEventListener("mouseup", _this.handleMouseup)
    },
    // 鼠标移动
    handleMousemove(e) {
      let _this = this
      setTimeout(() => {
        console.log(e);
      }, 200);
    }
  },
}
</script>

<style lang="scss" scoped>
  .da-scroller {
    position: relative;
    height: 100%;
    overflow: hidden;
    ::-webkit-scrollbar {
      width: 10px;
      height: 10px;
    }
    &:hover {
      .is-vertical {
        right: 0;
        transition: right .3s ease;
      }
    }
    &__wrap {
      height: 100%;
      overflow: auto;
      margin-right: -10px;
    }
    &__view {
      
    }
    &__bar {
      position: absolute;
      &.is-vertical {
        top: 0;
        right: -10px;
        width: 10px;
        height: 100%;
      }
      &.is-horizontal {
        bottom: 0;
        left: 0;
        width: 100%;
        height: 10px;
      }
    }
    &__track {
      position: relative;
      width: 100%;
      height: 100%;
      border-radius: 5px;
      background-color: rgba(200, 200, 200, 0.3);
    }
    &__thumb {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border-radius: 5px;
      background-color: rgba(2, 30, 31, 0.808);
      cursor: pointer;
      &:hover {
        opacity: .8;
      }
    }
  }
</style>